// Fonts
$mainFont: 'Rubik', sans-serif;

// Media Queries
$phone: 576px;
$tablet: 768px;
$desktop: 992px;
$desktopxl: 1200px;
$desktopxxl: 1400px;
$ultra: 1930px;

// Colors Text
$appSubheading: hsl(276, 100%, 81%);
$appChatLeft: hsl(276, 55%, 52%);
$appChatRight: hsl(271, 15%, 43%);
$appPlaceholderText: hsl(206, 6%, 79%);
$appBg: hsl(270, 20%, 96%);
$appSubmitButton: hsl(271, 36%, 24%);
$appOutlineRadioButton: hsl(289, 100%, 72%);

// Colors Gradients
$lightMagenta: #e942ff;
$lightViolet: #8838ff;

// Colors Secondary
$cHeading: hsl(271, 36%, 24%);
$cParagraph: hsl(270, 7%, 64%);
$black: hsl(0, 0%, 0%);
$white: hsl(0, 0%, 100%);