@use 'base/variables' as v;
@use 'base/mixins' as m;

.app {
    padding: 6.5rem 0;
    position: relative;
    background-color: v.$appBg;
    overflow: hidden;

    @include m.tablet {
        height: 100vh;
        padding: 12rem 0;
    }

    &::before {
        position: absolute;
        z-index: 20;
        right: 50%;
        top: -30rem;
        display: block;
        content: '';
        width: 70rem;
        height: 85rem;
        border-radius: 0 0 40rem 40rem;

        background-image: linear-gradient(215deg, #8838ff, #e942ff, #8838ff);
        background-size: 400% 400%;
        animation: gradient 15s ease infinite;
    }

    @keyframes gradient {
        0% {
            background-position: 0% 50%;
        }
        50% {
            background-position: 0% 100%;
        }
        100% {
            background-position: 0% 50%;
        }
    }

    &::after {
        position: absolute;
        z-index: 10;
        left: 46%;
        bottom: -33rem;
        display: block;
        content: '';
        width: 70rem;
        height: 87rem;
        background-color: #f2eef8;
        border-radius: 40rem 40rem 0 0;
    }

    @include m.tablet {
        &::before {
            top: 0;
            right: 71%;
        }

        &::after {
            left: 77%;
            bottom: 0;
        }
    }

    &__grid {
        position: relative;
        z-index: 30;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            align-items: center;
            gap: 3rem;
        }
    }

    &__content {
        margin-top: 6rem;
    }

    &__heading {
        font-size: 4rem;
        font-weight: 500;
        text-align: center;
        color: v.$cHeading;
    }

    &__text {
        margin: 2.4rem auto 0 auto;
        max-width: 40rem;
        font-size: 1.6rem;
        line-height: 1.7;
        text-align: center;
        color: v.$cParagraph;
    }

    @include m.tablet {
        &__content {
            margin-top: 0;
        }

        &__heading {
            font-size: 5.4rem;
            text-align: left;
        }

        &__text {
            margin-top: 2.7rem;
            max-width: fit-content;
            font-size: 2.2rem;
            text-align: left;
        }
    }
}

.attribution { font-size: 11px; text-align: center; }
.attribution a { color: hsl(228, 45%, 44%); }