@use 'base/variables' as v;
@use 'base/mixins' as m;

.attribution {
    padding: 2rem 0;
    background-color: #f5f3f7;

    @include m.tablet {
        display: flex;
        gap: 2.5rem;
        justify-content: center;
        bottom: 4rem;
    }

    &__text,
    &__link {
        font-size: 1.3rem;
    }

    &__text {
        color: v.$cParagraph;
    }

    &__link {
        color: v.$appChatLeft;
    }
}