@use 'base/variables' as v;
@use 'base/mixins' as m;

.chat {
    position: relative;
    display: block;
    margin: 0 auto;
    width: 24.5rem;
    border: 1rem solid v.$white;
    border-radius: 3rem;
    background-color: v.$appBg;
    box-shadow: 2px 8px 40px 2px rgba(0,0,0,0.15);

    &::before {
        position: absolute;
        top: -1rem;
        right: 0;
        bottom: 0;
        left: 0;
        content: '';
        height: 2.8rem;
        width: 57%;
        margin: 0 auto;
        border-radius: 0 0 1.3rem 1.3rem;
        background-color: v.$white;
    }

    @include m.tablet {
        width: 33rem;
        border: 1.3rem solid v.$white;
        border-radius: 4rem;

        &::before {
            top: -1.1rem;
            height: 3.4rem;
            border-radius: 0 0 1.8rem 1.8rem;
        }
    }
    
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;

        margin-bottom: 1.2rem;
        padding: 3rem 1.3rem 1rem 1.3rem;
        background-image: linear-gradient(to right, v.$lightViolet 0%, v.$lightMagenta 75%);
        border-radius: 1.8rem 1.8rem .6rem .6rem;
        box-shadow: 2px 5px 12px -4px rgba(0,0,0,0.2);
    }

    &__member {
        display: flex;
        align-items: center;
        gap: 1rem;
    }

    &__arrow {
        width: .5rem;
    }

    &__avatar {
        border-radius: 10rem;
        outline: .1rem solid v.$white;
        width: 2.2rem;
        height: 2.2rem;
    }

    &__name {
        font-size: 1.1rem;
        font-weight: 500;
        color: v.$white;
    }

    &__state {
        margin: 0;
        font-size: .8rem;
        color: v.$appSubheading;
    }

    &__menu {
        width: .4rem;
    }

    @include m.tablet {
        &__header {
            margin-bottom: 1.5rem;
            padding: 3.8rem 1.9rem 1.5rem 1.9rem;
            border-radius: 2.6rem 2.6rem .6rem .6rem;
        }

        &__member {
            gap: 1.3rem;
        }

        &__arrow {
            width: .6rem;
        }
    
        &__avatar {
            outline: .2rem solid v.$white;
            width: 3rem;
            height: 3rem;
        }

        &__name {
            font-size: 1.5rem;
        }
    
        &__state {
            font-size: 1rem;
        }

        &__menu {
            width: .5rem;
        }
    }

    // CONTENT

    &__left,
    &__right {
        padding: .6rem .8rem;
        max-width: 12.8rem;
        font-size: .8rem;
        line-height: 1.4;
        opacity: 0;

        animation-name: chat;       
        animation-duration: 700ms;                              
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;
    }

    &__left {
        margin: .8rem 0 0 .7rem;
        border-radius: .8rem .8rem .8rem .5rem;
        color: v.$appChatLeft;
        background-color: #ede4f5;

        &:nth-child(2) {
            animation-delay: 550ms;
        }

        &:nth-child(6) {
            animation-delay: 3800ms;
        }
    }

    @keyframes chat {
        0% {
            opacity: 0;
            transform: translateY(.8rem);
        }
        100% {
            opacity: 1;
            transform: translateY(0rem);
        }
    }

    &__galery {
        display: flex;
        justify-content: flex-end;
        gap: .8rem;
        margin: 1.6rem .7rem 0 0;
    }

    &__image {
        border-radius: 1rem;
        width: 4rem;
        opacity: 0;

        animation-name: imgGalery;       
        animation-duration: 500ms;                              
        animation-timing-function: cubic-bezier(0.375, 0.150, 0.690, 1.650);
        animation-delay: 1500ms;
        animation-fill-mode: forwards;

        &:nth-child(2) {
            animation-delay: 1800ms;
        }

        &:nth-child(3) {
            animation-delay: 2100ms;
        }
        // animation-fill-mode: forwards;

        // animation: imgGalery .4s 2s cubic-bezier(0.375, 0.150, 0.690, 1.650);
    }

    @keyframes imgGalery {
        0% {
            opacity: 0;
            transform: scale(.8);
        }
        100% {
            opacity: 1;
            transform: scale(1);
        }
    }

    &__right {
        margin: .8rem .7rem 0 auto;
        border-radius: 1rem 1rem .5rem 1rem;
        background-color: v.$white;
        color: v.$appChatRight;
        box-shadow: 2px 3px 12px -4px rgba(0,0,0,0.2);

        &:nth-child(4) {
            animation-delay: 2700ms;
        }

        &:nth-child(5) {
            max-width: fit-content;
            margin-bottom: 1.5rem;
            animation-delay: 3100ms;
        }
    }

    @include m.tablet {
        &__left,
        &__right {
            padding: .8rem 1rem;
            max-width: 16.8rem;
            font-size: 1.05rem;
        }

        &__left {
            margin: 1.1rem 0 0 1.1rem;
            border-radius: 1.2rem 1.2rem 1.2rem .9rem;
        }

        &__galery {
            gap: 1.1rem;
            margin: 2.2rem 1.1rem 0 0;
        }

        &__image {
            border-radius: 1.2rem;
            width: 5.4rem;
        }

        &__right {
            margin: 1rem 1.1rem 0 auto;
            border-radius: 1.2rem 1.2rem .6rem 1.2rem;

            &:nth-child(5) {
                margin-bottom: 2rem;
            }
        }
    }

    &__plan {
        display: flex;
        justify-content: space-between;
        align-items: center;
            overflow: hidden;
        margin: .8rem 0 0 .7rem;
        padding: .6rem .8rem;
        color: v.$white;
        border-radius: .8rem .8rem .8rem .5rem;
        background-image: linear-gradient(to right, v.$lightMagenta 30%, v.$lightViolet 100%);
        opacity: 0;
        animation-name: animationPlan;
        animation-duration: 1200ms;
        animation-timing-function: ease;
        animation-fill-mode: forwards;

        &:nth-child(7) {
            animation-delay: 4400ms;
        }

        &:nth-child(8) {
            animation-delay: 4800ms;
        }
    }

    @keyframes animationPlan {
        0% {
            opacity: 0;
            max-width: 0;
        }
        
        100% {
            opacity: 1;
            max-width: 17.8rem;
        }
    }

    @include m.tablet {
        @keyframes animationPlan {
            0% {
                opacity: 0;
                max-width: 0;
            }
            
            100% {
                opacity: 1;
                max-width: 19rem;
            }
        }
    }

    &__time {
        min-width: 12rem;
        font-size: .8rem;
        line-height: 1.4;

        &::before {
            display: inline-block;
            content: '';
            width: 1.2rem;
            height: 1.2rem;
            margin-right: 1rem;
            border: 1px solid v.$appOutlineRadioButton;
            border-radius: 1rem;
            vertical-align: middle;
        }
    }

    &__price {
        margin: 0;
        padding: 1rem;
        font-size: 1.2rem;
        font-weight: 700;
        line-height: 0;
    }

    @include m.tablet {
        &__plan {
            margin: 1rem 0 0 1.1rem;
            padding: 1.2rem 1.1rem;
            max-width: 21rem;
            border-radius: 1.2rem 1.2rem 1.2rem .6rem;
        }

        &__time {
            font-size: 1.1rem;
    
            &::before {
                width: 1.6rem;
                height: 1.6rem;
                border: 2px solid v.$appOutlineRadioButton;
            }
        }
    
        &__price {
            font-size: 1.6rem;
        }
    }

    // FOOTER
    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 1.5rem .8rem 1rem .8rem;
        padding: .5rem .5rem .5rem 2rem;
        background-color: v.$white;
        border-radius: 20rem;
    }

    &__message {
        font-size: 1rem;
        color: v.$appPlaceholderText;

        opacity: 0;
        overflow: hidden;
        white-space: nowrap;
        margin: 0 auto 0 0; 
        animation-name: typing;
        animation-duration: 4300ms;
        animation-delay: 5900ms;
        animation-fill-mode: forwards;
        animation-timing-function: steps(40, end);
    }

    @keyframes typing {
        0% {
            opacity: 1;
            width: 0;
        }
        100% {
            opacity: 1;
            width: 100%;
        }
      }

    &__send-box {
        padding: .7rem;
        background-color: v.$appSubmitButton;
        border-radius: 2rem;
    }

    &__send {
        height: 1rem;
        width: 1rem;
    }

    @include m.tablet {
        &__footer {
            margin: 2rem 1rem 1.4rem 1rem;
            padding: .7rem 1rem .7rem 2.8rem;
        }

        &__message {
            font-size: 1.2rem;
        }
    
        &__send-box {
        padding: .9rem;
        border-radius: 2rem;
        }
    
        &__send {
            height: 1.4rem;
            width: 1.4rem;
        }
    }
}