@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopxl {
    @media (min-width: v.$desktopxl) {
        @content;
    }
}

@mixin desktopxxl {
    @media (min-width: v.$desktopxxl) {
        @content;
    }
}

@mixin ultra {
    @media (min-width: v.$ultra) {
        @content;
    }
}

/* Buttons */
@mixin btn($bgColor) {
    padding: 1.7rem 4rem;
    font-size: 1.7rem;
    font-weight: 600;
    border-radius: 5rem;
    background-color: $bgColor;
    border-bottom: .4rem solid darken($bgColor, 5%);
    color: v.$white;
    letter-spacing: .15rem;
    transition-property: background-color, box-shadow;
    transition-duration: .3s;
    transition-timing-function: ease-in-out;

    @include tablet {
        padding: 2.2rem 4.5rem;
        font-size: 2.2rem;
    }

    &:hover {
        background-color: lighten($bgColor, 2%);
        box-shadow: 0px 9px 13px -2px rgba($bgColor, 0.3);
    }
}

/* Grid */
@mixin grid($columns, $spacing ) {
    display: grid;
    gap: $spacing;
    grid-template-columns: repeat($columns, 1fr);
}

/* Headings and paragraphs */
@mixin heading {
    width: 90%;
    margin: 0 auto 2rem auto;
    font-size: 2.8rem;
    font-weight: 600;
    text-align: center;
    color: v.$darkGray;

    @include phone {
        margin-bottom: 2.5rem;
        font-size: 3.6rem;
    }

    @include desktop {
        margin-bottom: 3rem;
        font-size: 4.6rem;
    }
}

@mixin subheading {
    width: 87%;
    margin: 0 auto;
    text-align: center;
    color: v.$lightGray;

    @include phone {
        font-size: 2rem;
        max-width: 93rem;
    }

    @include desktop {
        font-size: 2.4rem;
    }
}