/* Headings and paragraphs */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.container {
  width: 83%;
  max-width: 1226px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  width: 100%; /* Reescribimos el width y heigth que lleva la etiqueta html, la cual le da mas posicionamiento, pero nos deforma las imagenes */
  height: auto; /* Reescribimos el width y heigth que lleva la etiqueta html, la cual le da mas posicionamiento, pero nos deforma las imagenes */
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
  color: hsl(0deg, 0%, 0%);
}

p {
  margin: 0;
  font-size: 1.6rem;
  line-height: 1.6;
}

.app {
  padding: 6.5rem 0;
  position: relative;
  background-color: hsl(270deg, 20%, 96%);
  overflow: hidden;
}
@media (min-width: 768px) {
  .app {
    height: 100vh;
    padding: 12rem 0;
  }
}
.app::before {
  position: absolute;
  z-index: 20;
  right: 50%;
  top: -30rem;
  display: block;
  content: "";
  width: 70rem;
  height: 85rem;
  border-radius: 0 0 40rem 40rem;
  background-image: linear-gradient(215deg, #8838ff, #e942ff, #8838ff);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 0% 100%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.app::after {
  position: absolute;
  z-index: 10;
  left: 46%;
  bottom: -33rem;
  display: block;
  content: "";
  width: 70rem;
  height: 87rem;
  background-color: #f2eef8;
  border-radius: 40rem 40rem 0 0;
}
@media (min-width: 768px) {
  .app::before {
    top: 0;
    right: 71%;
  }
  .app::after {
    left: 77%;
    bottom: 0;
  }
}
.app__grid {
  position: relative;
  z-index: 30;
}
@media (min-width: 768px) {
  .app__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 3rem;
  }
}
.app__content {
  margin-top: 6rem;
}
.app__heading {
  font-size: 4rem;
  font-weight: 500;
  text-align: center;
  color: hsl(271deg, 36%, 24%);
}
.app__text {
  margin: 2.4rem auto 0 auto;
  max-width: 40rem;
  font-size: 1.6rem;
  line-height: 1.7;
  text-align: center;
  color: hsl(270deg, 7%, 64%);
}
@media (min-width: 768px) {
  .app__content {
    margin-top: 0;
  }
  .app__heading {
    font-size: 5.4rem;
    text-align: left;
  }
  .app__text {
    margin-top: 2.7rem;
    max-width: fit-content;
    font-size: 2.2rem;
    text-align: left;
  }
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228deg, 45%, 44%);
}

.chat {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 24.5rem;
  border: 1rem solid hsl(0deg, 0%, 100%);
  border-radius: 3rem;
  background-color: hsl(270deg, 20%, 96%);
  box-shadow: 2px 8px 40px 2px rgba(0, 0, 0, 0.15);
}
.chat::before {
  position: absolute;
  top: -1rem;
  right: 0;
  bottom: 0;
  left: 0;
  content: "";
  height: 2.8rem;
  width: 57%;
  margin: 0 auto;
  border-radius: 0 0 1.3rem 1.3rem;
  background-color: hsl(0deg, 0%, 100%);
}
@media (min-width: 768px) {
  .chat {
    width: 33rem;
    border: 1.3rem solid hsl(0deg, 0%, 100%);
    border-radius: 4rem;
  }
  .chat::before {
    top: -1.1rem;
    height: 3.4rem;
    border-radius: 0 0 1.8rem 1.8rem;
  }
}
.chat__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.2rem;
  padding: 3rem 1.3rem 1rem 1.3rem;
  background-image: linear-gradient(to right, #8838ff 0%, #e942ff 75%);
  border-radius: 1.8rem 1.8rem 0.6rem 0.6rem;
  box-shadow: 2px 5px 12px -4px rgba(0, 0, 0, 0.2);
}
.chat__member {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.chat__arrow {
  width: 0.5rem;
}
.chat__avatar {
  border-radius: 10rem;
  outline: 0.1rem solid hsl(0deg, 0%, 100%);
  width: 2.2rem;
  height: 2.2rem;
}
.chat__name {
  font-size: 1.1rem;
  font-weight: 500;
  color: hsl(0deg, 0%, 100%);
}
.chat__state {
  margin: 0;
  font-size: 0.8rem;
  color: hsl(276deg, 100%, 81%);
}
.chat__menu {
  width: 0.4rem;
}
@media (min-width: 768px) {
  .chat__header {
    margin-bottom: 1.5rem;
    padding: 3.8rem 1.9rem 1.5rem 1.9rem;
    border-radius: 2.6rem 2.6rem 0.6rem 0.6rem;
  }
  .chat__member {
    gap: 1.3rem;
  }
  .chat__arrow {
    width: 0.6rem;
  }
  .chat__avatar {
    outline: 0.2rem solid hsl(0deg, 0%, 100%);
    width: 3rem;
    height: 3rem;
  }
  .chat__name {
    font-size: 1.5rem;
  }
  .chat__state {
    font-size: 1rem;
  }
  .chat__menu {
    width: 0.5rem;
  }
}
.chat__left, .chat__right {
  padding: 0.6rem 0.8rem;
  max-width: 12.8rem;
  font-size: 0.8rem;
  line-height: 1.4;
  opacity: 0;
  animation-name: chat;
  animation-duration: 700ms;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}
.chat__left {
  margin: 0.8rem 0 0 0.7rem;
  border-radius: 0.8rem 0.8rem 0.8rem 0.5rem;
  color: hsl(276deg, 55%, 52%);
  background-color: #ede4f5;
}
.chat__left:nth-child(2) {
  animation-delay: 550ms;
}
.chat__left:nth-child(6) {
  animation-delay: 3800ms;
}
@keyframes chat {
  0% {
    opacity: 0;
    transform: translateY(0.8rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}
.chat__galery {
  display: flex;
  justify-content: flex-end;
  gap: 0.8rem;
  margin: 1.6rem 0.7rem 0 0;
}
.chat__image {
  border-radius: 1rem;
  width: 4rem;
  opacity: 0;
  animation-name: imgGalery;
  animation-duration: 500ms;
  animation-timing-function: cubic-bezier(0.375, 0.15, 0.69, 1.65);
  animation-delay: 1500ms;
  animation-fill-mode: forwards;
}
.chat__image:nth-child(2) {
  animation-delay: 1800ms;
}
.chat__image:nth-child(3) {
  animation-delay: 2100ms;
}
@keyframes imgGalery {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.chat__right {
  margin: 0.8rem 0.7rem 0 auto;
  border-radius: 1rem 1rem 0.5rem 1rem;
  background-color: hsl(0deg, 0%, 100%);
  color: hsl(271deg, 15%, 43%);
  box-shadow: 2px 3px 12px -4px rgba(0, 0, 0, 0.2);
}
.chat__right:nth-child(4) {
  animation-delay: 2700ms;
}
.chat__right:nth-child(5) {
  max-width: fit-content;
  margin-bottom: 1.5rem;
  animation-delay: 3100ms;
}
@media (min-width: 768px) {
  .chat__left, .chat__right {
    padding: 0.8rem 1rem;
    max-width: 16.8rem;
    font-size: 1.05rem;
  }
  .chat__left {
    margin: 1.1rem 0 0 1.1rem;
    border-radius: 1.2rem 1.2rem 1.2rem 0.9rem;
  }
  .chat__galery {
    gap: 1.1rem;
    margin: 2.2rem 1.1rem 0 0;
  }
  .chat__image {
    border-radius: 1.2rem;
    width: 5.4rem;
  }
  .chat__right {
    margin: 1rem 1.1rem 0 auto;
    border-radius: 1.2rem 1.2rem 0.6rem 1.2rem;
  }
  .chat__right:nth-child(5) {
    margin-bottom: 2rem;
  }
}
.chat__plan {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  margin: 0.8rem 0 0 0.7rem;
  padding: 0.6rem 0.8rem;
  color: hsl(0deg, 0%, 100%);
  border-radius: 0.8rem 0.8rem 0.8rem 0.5rem;
  background-image: linear-gradient(to right, #e942ff 30%, #8838ff 100%);
  opacity: 0;
  animation-name: animationPlan;
  animation-duration: 1200ms;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}
.chat__plan:nth-child(7) {
  animation-delay: 4400ms;
}
.chat__plan:nth-child(8) {
  animation-delay: 4800ms;
}
@keyframes animationPlan {
  0% {
    opacity: 0;
    max-width: 0;
  }
  100% {
    opacity: 1;
    max-width: 17.8rem;
  }
}
@media (min-width: 768px) {
  @keyframes animationPlan {
    0% {
      opacity: 0;
      max-width: 0;
    }
    100% {
      opacity: 1;
      max-width: 19rem;
    }
  }
}
.chat__time {
  min-width: 12rem;
  font-size: 0.8rem;
  line-height: 1.4;
}
.chat__time::before {
  display: inline-block;
  content: "";
  width: 1.2rem;
  height: 1.2rem;
  margin-right: 1rem;
  border: 1px solid hsl(289deg, 100%, 72%);
  border-radius: 1rem;
  vertical-align: middle;
}
.chat__price {
  margin: 0;
  padding: 1rem;
  font-size: 1.2rem;
  font-weight: 700;
  line-height: 0;
}
@media (min-width: 768px) {
  .chat__plan {
    margin: 1rem 0 0 1.1rem;
    padding: 1.2rem 1.1rem;
    max-width: 21rem;
    border-radius: 1.2rem 1.2rem 1.2rem 0.6rem;
  }
  .chat__time {
    font-size: 1.1rem;
  }
  .chat__time::before {
    width: 1.6rem;
    height: 1.6rem;
    border: 2px solid hsl(289deg, 100%, 72%);
  }
  .chat__price {
    font-size: 1.6rem;
  }
}
.chat__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1.5rem 0.8rem 1rem 0.8rem;
  padding: 0.5rem 0.5rem 0.5rem 2rem;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 20rem;
}
.chat__message {
  font-size: 1rem;
  color: hsl(206deg, 6%, 79%);
  opacity: 0;
  overflow: hidden;
  white-space: nowrap;
  margin: 0 auto 0 0;
  animation-name: typing;
  animation-duration: 4300ms;
  animation-delay: 5900ms;
  animation-fill-mode: forwards;
  animation-timing-function: steps(40, end);
}
@keyframes typing {
  0% {
    opacity: 1;
    width: 0;
  }
  100% {
    opacity: 1;
    width: 100%;
  }
}
.chat__send-box {
  padding: 0.7rem;
  background-color: hsl(271deg, 36%, 24%);
  border-radius: 2rem;
}
.chat__send {
  height: 1rem;
  width: 1rem;
}
@media (min-width: 768px) {
  .chat__footer {
    margin: 2rem 1rem 1.4rem 1rem;
    padding: 0.7rem 1rem 0.7rem 2.8rem;
  }
  .chat__message {
    font-size: 1.2rem;
  }
  .chat__send-box {
    padding: 0.9rem;
    border-radius: 2rem;
  }
  .chat__send {
    height: 1.4rem;
    width: 1.4rem;
  }
}

.attribution {
  padding: 2rem 0;
  background-color: #f5f3f7;
}
@media (min-width: 768px) {
  .attribution {
    display: flex;
    gap: 2.5rem;
    justify-content: center;
    bottom: 4rem;
  }
}
.attribution__text, .attribution__link {
  font-size: 1.3rem;
}
.attribution__text {
  color: hsl(270deg, 7%, 64%);
}
.attribution__link {
  color: hsl(276deg, 55%, 52%);
}